.result_wrap {
  position: relative;
  width: 100%;
}
.card_absolute {
  position: absolute;
  top: -80px;
  left: 387px;
  margin: 0 auto;
}

@media screen and (min-width: 375px) and (max-width: 720px) {
  .card_absolute {
    top: -120px;
    left: 0;
    padding: 30px;
    width: 100%;
  }
}
